import Vue from "vue"
import Vuetify, { VIcon, VSnackbar, VCard, VDialog, VBtn, VCardTitle, VCardText, VCardActions, VSpacer, VToolbar, VToolbarTitle } from "vuetify/lib"
import "../theme/default.styl"
import 'vuetify/src/stylus/app.styl'

Vue.use(Vuetify, {
  theme: {
    primary: "#ee44aa",
    secondary: "#424242",
    accent: "#82B1FF",
    error: "#FF5252",
    info: "#2196F3",
    success: "#4CAF50",
    warning: "#FFC107"
  },
  customProperties: true,
  components: {
    // the components which Toast.vue used
    VIcon,
    VSnackbar,
    VCard,
    VBtn,
    VDialog,
    VCardTitle,
    VCardText,
    VCardActions,
    VSpacer,
    VToolbar,
    VToolbarTitle
  }
})
