
import services from '../../api/services'


const state = {
  share: [],
  currentShare: null
}

const getters = {
  getShares: state => state.share,
  getCurrentShare: state => state.currentShare
}

const mutations = {
  post_share (state, share) { // Ajustar
    state.share.push(share)
  },
  get_shares (state, share) {
    // console.log('========', share)
    state.share = share
  },
  get_share (state, share) {
    state.currentShare = share
  },
  edit_share (state, share) {
    var index = state.share.findIndex(share => share.id === share.id)
    state.share[index] = share
    // state.share.splice(index, 1)
    // state.share.push(Share)
  },
  delete_share (state, currentShare) {
    var index = state.share.findIndex(share => share.id === currentShare.id)
    state.share.splice(index, 1)
  }
}

const actions = {
  postShare ({
    commit,
    state
  }, share) {
    return services.share.post(share)
      .then((response) => {
        commit('post_share', response.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getShares ({
    commit,
    state
  }, filter) {
    return services.share.getAll(filter)
      .then((response) => {
        commit('get_shares', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getShare ({
    commit,
    state
  }, id) {
    return services.share.get(id)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },
  editShare ({
    commit,
    state
  }, id) {
    return services.share.edit(id)
      .then((response) => {
        commit('edit_share', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  deleteShare ({
    commit,
    state
  }, id) {
    return services.share.delete(id)
      .then((response) => {
        commit('delete_share', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
