
import services from './../../api/services'


const state = {
  images: [],
  currentImage: null
}

const getters = {
  getImages: state => state.images,
  getCurrentImage: state => state.currentImage
}

const mutations = {
  post_image (state, image) { // Ajustar
    state.images.push(image)
  },
  get_images (state, images) {
    // console.log("+++++++++--------", images)
    state.images = images
  },
  get_image (state, image) {
    state.currentimage = image
  },
  edit_image (state, imageF) {
    var index = state.images.findIndex(image => image.id === imageF.id)
    state.images[index] = imageF
    // state.images.splice(index, 1)
    // state.images.push(image)
  },
  delete_image (state, imageId) {
    var index = state.images.findIndex(image => image.id === imageId)
    state.images.splice(index, 1)
  }
}

const actions = {
  postImage ({
    commit,
    state
  }, image) {
    return services.image.post(image)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },
  getImages ({
    commit,
    state
  }, filter) {
    return services.image.getAll(filter)
      .then((response) => {
        commit('get_images', response.data)
      })
      .catch((error) => Promise.reject(error))
  },
  getImage ({
    commit,
    state
  }, id) {
    return services.image.get(id)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },
  editImage ({
    commit,
    state
  }, id) {
    return services.image.edit(id)
      .then((response) => {
        commit('edit_image', response.data)
      })
      .catch((error) => Promise.reject(error))
  },
  deleteImage ({
    commit,
    state
  }, id) {
    return services.image.delete(id)
      .then((response) => {
        commit('delete_image', id)
      })
      .catch((error) => Promise.reject(error))
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
