
import services from './../../api/services'


const state = {
  emails: []
}

const getters = {
  getEmails: state => state.emails
}

const mutations = {
  post_email (state, email) { // Ajustar
    state.emails.push(email)
  },
  get_emails (state, emails) {
    state.emails = emails
  },
  // get_email (state, email) {
  //   state.currentEmail = email
  // },
  edit_email (state, email) {
    var index = state.emails.findIndex(email => email.id === emails.id)
    state.emails[index] = email
    // state.emails.splice(index, 1)
    // state.emails.push(email)
  },
  delete_email (state, email) {
    var index = state.emails.findIndex(email => email.id === emails.id)
    state.emails.splice(index, 1)
  }
}

