import services from "./../../api/services"

const state = {
  orthomosaics: [],
  currentOrthomosaic: null
}

const getters = {
  getOrthomosaics: state => state.orthomosaics,
  getCurrentOrthomosaic: state => state.currentOrthomosaic
}

const mutations = {
  post_orthomosaic(state, orthomosaic) {
    // Ajustar
    state.orthomosaics.push(orthomosaic)
  },
  get_orthomosaics(state, orthomosaics) {
    state.orthomosaics = orthomosaics
  },
  get_orthomosaic(state, orthomosaic) {
    state.currentOrthomosaic = orthomosaic
  },
  edit_orthomosaic(state, ortho) {
    var index = state.orthomosaics.findIndex(orthomosaic => orthomosaic.id === ortho.id)
    state.orthomosaics[index] = ortho
    // state.Orthomosaics.splice(index, 1)
    // state.Orthomosaics.push(Orthomosaic)
  },
  delete_orthomosaic(state, orthoId) {
    var index = state.orthomosaics.findIndex(orthomosaic => orthomosaic.id === orthoId)
    state.orthomosaics.splice(index, 1)
  }
}

const actions = {
  postOrthomosaic({ commit, state }, orthomosaic) {
    return services.orthomosaics
      .post(orthomosaic)
      .then(response => Promise.resolve(response.data))
      .catch(error => Promise.reject(error))
    // return services.orthomosaics.post(orthomosaic)
    //   .then((response) => {
    //     commit('post_orthomosaic', response.data)
    //     this.resolve(response.data)
    //   })
    //   .catch((error) => {
    //     console.error(error)
    //   })
  },
  getOrthomosaics({ commit, state }, filter) {
    return services.orthomosaics
      .getAll(filter)
      .then(response => {
        commit("get_orthomosaics", response.data)
        return Promise.resolve(response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  getOrthomosaic({ commit, state }, id) {
    return services.orthomosaics
      .get(id)
      .then(response => Promise.resolve(response.data))
      .catch(error => Promise.reject(error))
  },
  editOrthomosaic({ commit, state }, id) {
    return services.orthomosaics
      .edit(id)
      .then(response => {
        commit("edit_orthomosaic", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  deleteOrthomosaic({ commit, state }, id) {
    return services.orthomosaics
      .delete(id)
      .then(response => {
        commit("delete_orthomosaic", id)
      })
      .catch(error => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
