import { AuthLayout, DefaultLayout, ChatLayout } from "@/components/layouts"

export const publicRoute = [
  {
    path: "*",
    component: () => import(/* webpackChunkName: "errors-404" */ "@/views/error/NotFound.vue")
  },
  {
    path: "/auth",
    component: AuthLayout,
    meta: {
      requiresAuth: false,
      title: "Login"
    },
    redirect: "/auth/login",
    hidden: true,
    children: [
      {
        path: "login",
        name: "Login",
        meta: {
          requiresAuth: false,
          title: "Login"
        },
        component: () => import(/* webpackChunkName: "login" */ "@/views/auth/Login.vue")
      }
    ]
  },

  {
    path: "/404",
    name: "NotFound",
    meta: {
      title: "Not Found"
    },
    component: () => import(/* webpackChunkName: "errors-404" */ "@/views/error/NotFound.vue")
  },

  {
    path: "/403",
    name: "AccessDenied",
    meta: {
      title: "Access Denied"
    },
    component: () => import(/* webpackChunkName: "errors-404" */ "@/views/error/Deny.vue")
  },

  {
    path: "/500",
    name: "ServerError",
    meta: {
      requiresAuth: false,
      title: "Server Error"
    },
    component: () => import(/* webpackChunkName: "errors-500" */ "@/views/error/Error.vue")
  }
]

