import services from "./../../api/services"
import { promises } from "fs"

const state = {
  users: [],
  currentUser: null
}

const getters = {
  getUsers: state => state.users,
  getCurrentUser: state => state.currentUser
}

const mutations = {
  post_user(state, user) {
    // Ajustar
    state.users.push(user)
  },
  get_users(state, users) {
    state.users = users
  },
  get_user(state, user) {
    state.currentUser = user
  },
  edit_user(state, users) {
    var index = state.users.findIndex(user => user.id === users.id)
    state.users[index] = users
  },
  delete_user(state, userId) {
    var index = state.users.findIndex(user => user.id === userId)
    state.users.splice(index, 1)
  }
}

const actions = {
  postUser({ commit, state }, user) {
    return services.users
      .post(user)
      .then(response => {
        commit("post_user", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  getUsers({ commit, state }, filter) {
    return services.users
      .getAll(filter)
      .then(response => {
        commit("get_users", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  getUser({ commit, state }, id) {
    return services.users
      .get(id)
      .then(response => Promise.resolve(response.data))
      .catch(error => Promise.reject(error))
  },
  editUser({ commit, state }, id) {
    return services.users
      .edit(id)
      .then(response => {
        commit("edit_user", response.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  deleteUser({ commit, state }, id) {
    return services.users
      .delete(id)
      .then(response => {
        commit("delete_user", id)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },

  countUsers({ state }) {
    return services.users
      .count()
      .then(response => {
        return Promise.resolve(response)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch(error => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
