const state = {
  projects: [],
  currentProject: null
}

const getters = {
  getProjects: state => state.projects,
  getCurrentProject: state => state.currentProject
}

const mutations = {
  post_project(state, project) {
    // Ajustar
    state.projects.push(project)
  },
  get_projects(state, projects) {
    state.projects = projects
  },
  get_project(state, project) {
    state.currentProject = project
  },
  edit_project(state, projectO) {
    var index = state.projects.findIndex(project => project.id === projectO.id)
    state.projects[index] = projectO
    // state.projects.splice(index, 1)
    // state.projects.push(project)
  },
  delete_project(state, projectId) {
    var index = state.projects.findIndex(project => project.id === projectId)
    state.projects.splice(index, 1)
  }
}

const actions = {
  postProject({ commit, state }, project) {
    return services.projects
      .post(project)
      .then(response => {
        commit("post_project", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  getProjects({ commit, state }, filter) {
    return services.projects
      .getAll(filter)
      .then(response => {
        commit("get_projects", response.data)
        return Promise.resolve(response.data)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  getProject({ commit, state }, id) {
    // console.log('++++', id)
    return services.projects
      .get(id)
      .then(response => Promise.resolve(response.data))
      .catch(error => Promise.reject(error))
  },

  getProjectOwner({ commit, state }, id) {
    return services.projects
      .getOwner(id)
      .then(response => {
        commit("get_projects", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  editProject({ commit, state }, project) {
    return services.projects
      .edit(project)
      .then(response => {
        commit("edit_project", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  deleteProject({ commit, state }, id) {
    return services.projects
      .delete(id)
      .then(response => {
        commit("delete_project", id)
      })
      .catch(error => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
