import axios from "axios"

export default {
  post(user) {
    return axios
      .post("/api/v2/user", user)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error))
  },
  getAll(filter = "") {
    return axios
      .get("/api/v2/users?" + filter)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error))
  },

  get(id) {
    return axios
      .get("/api/v2/user/" + id)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error))
  },

  edit(user) {
    return axios
      .put("/api/v2/user/" + user.id, user)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error))
  },

  delete(id) {
    return axios
      .delete("/api/v2/user/" + id)
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error))
  },

  count() {
    return axios
      .get("/api/v2/user/countusers")
      .then(response => Promise.resolve(response))
      .catch(error => Promise.reject(error))
  }
}
