//    baseUrl = 'http://localhost:8000/api/'
// }

// console.log(baseUrl)

Vue.use(VuetifyToast, {
  x: "right",
  y: "bottom",
  color: "info",
  icon: "info",
  timeout: 3000,
  dismissable: true,
  multiLine: false,
  vertical: false,
  queueable: true,
  showClose: true,
  shorts: {
    custom: {
      color: "purple"
    }
  },
  property: "$toast"
})

Validator.localize('pt_BR', pt_BR)
Validator.localize(dictionary)

VeeValidate.Validator.extend('verify_password', {
  getMessage: field => `A senha deve conter pelo menos: 1 letra maiscula, 1 letra minscula e 1 nmero`,
  validate: value => {
    var strongRegex = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.{8,})");
    // var strongRegex = new RegExp("^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})");
    return strongRegex.test(value);
  }
});

VeeValidate.Validator.extend('verify_email', {
  getMessage: field => `O email no deve conter o caractere "+".`,
  validate: value => {
    if (value.match(/[^a-z0-9\@\.\_\-]/g)){
      return false
    }else{
      return true
    }
  }
});

VeeValidate.Validator.extend('hotmail_email', {
  getMessage: field => `Infelizmente o hotmail no pode ser utilizado, tente outro email.`,
  validate: value => {
    var res = value.match(/hotmail/);
    if (res && res[0]=='hotmail'){
      return false
    }else{
      return true
    }
  }
});

// var base = axios.create({
