import services from "./../../api/services"

const state = {
  tasks: [],
  currentTask: null
}

const getters = {
  getTasks: state => state.tasks,
  getCurrentTask: state => state.currentTask
}

const mutations = {
  post_task(state, task) {
    // Ajustar
    // console.log('++++---', task)
    state.tasks.push(task)
  },
  get_run(state, task) {
    console.log(task)
  },
  get_tasks(state, tasks) {
    state.tasks = tasks
  },
  get_task(state, task) {
    state.currentTask = task
  },
  edit_task(state, taskO) {
    var index = state.tasks.findIndex(task => task.id === taskO.id)
    state.tasks[index] = taskO
  },
  delete_task(state, taskId) {
    var index = state.tasks.findIndex(task => task.id === taskId)
    state.tasks.splice(index, 1)
  }
}

const actions = {
  postTask({ commit, state }, task) {
    return services.tasks
      .post(task)
      .then(response => {
        commit("post_task", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  getRun({ commit, state }, task) {
    return services.tasks
      .run(task)
      .then(response => {
        commit("get_run", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  postRun({ commit, state }, obj) {
    return services.tasks
      .run2(obj.taskId, obj.instance)
      .then(response => {
        // commit('get_run', response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  getTasks({ commit, state }, filter) {
    return services.tasks
      .getAll(filter)
      .then(response => {
        commit("get_tasks", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  getTask({ commit, state }, id) {
    return services.tasks
      .get(id)
      .then(response => Promise.resolve(response.data))
      .catch(error => Promise.reject(error))
  },
  editTask({ commit, state }, id) {
    return services.tasks
      .edit(id)
      .then(response => {
        commit("edit_task", response.data)
      })
      .catch(error => {
        console.error(error)
      })
  },
  deleteTask({ commit, state }, id) {
    return services.tasks
      .delete(id)
      .then(response => {
        commit("delete_task", id)
      })
      .catch(error => {
        console.error(error)
      })
  },
  countTasks({ state }) {
    return services.tasks
      .count()
      .then(response => {
        return Promise.resolve(response)
        // commit(types.FETCH_PRODUCT, response.data)
      })
      .catch(error => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
