import services from './../../api/services'


const state = {
  geometry: [],
  currentGeometry: null
}

const getters = {
  getGeometrys: state => state.geometry,
  getCurrentGeometry: state => state.currentgeometry
}

const mutations = {
  post_geometry (state, geometry) { // Ajustar
    state.geometry.push(geometry)
  },
  get_geometry (state, geometry) {
    state.geometry = geometry
  },
  get_geometrys (state, geometry) {
    state.currentGeometry = geometry
  },
  get_geometry_project (state, geometry) {
    state.geometry = geometry
  },
  edit_geometry (state, currentGeometry) {
    var index = state.geometry.findIndex(geometry => geometry.id === currentGeometry.id)
    state.geometry[index] = currentGeometry
    // state.geometry.splice(index, 1)
    // state.geometry.push(geometry)
  },

  delete_geometry (state, currentGeometry) {
    var index = state.geometry.findIndex(geometry => geometry.id === currentGeometry.id)
    state.geometry.splice(index, 1)
  }
}

const actions = {
  postgeometry ({
    commit,
    state
  }, geometry) {
    return services.geometry.post(geometry)
      .then((response) => {
        commit('post_geometry', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getgeometrys ({
    commit,
    state
  }, filter) {
    return services.geometry.getAll(filter)
      .then((response) => {
        commit('get_geometry', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },
  getgeometry ({
    commit,
    state
  }, id) {
    return services.geometry.get(id)
      .then((response) => Promise.resolve(response.data))
      .catch((error) => Promise.reject(error))
  },

  editgeometry ({
    commit,
    state
  }, geometry) {
    return services.geometry.edit(geometry)
      .then((response) => {
        console.log(response)
        commit('edit_geometry', responde.data)
      })
      .catch((error) => {
        console.error(error)
      })
  },

  deletegeometry ({
    commit,
    state
  }, id) {
    return services.geometry.delete(id)
      .then((response) => {
        console.log(response)
        commit('delete_geometry', response.data)
      })
      .catch((error) => {
        console.error(error)
      })
  }
}

export default {
  state,
  getters,
  mutations,
  actions
}
